html,
body,
#app,
.react-mosaic-app {
  margin: 0;
  height: 100%;
  width: 100%;
}
.react-mosaic-app .sliderro .bp3-label {
  color: black;
  font-weight: 500;
}
.react-mosaic-app .sliderro .bp3-slider-axis .bp3-slider-label {
  color: black;
}
.react-mosaic-app .actions-label {
  margin-right: 10px;
}
.react-mosaic-app .theme-selection {
  margin: 0;
}
.react-mosaic-app .navbar-separator {
  height: 20px;
  border-left: 1px solid #5c7080;
  margin: 10px;
}
.react-mosaic-app .bp3-navbar {
  overflow: hidden;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.react-mosaic-app .bp3-navbar-heading a {
  color: #ffffff;
}
.react-mosaic-app .bp3-navbar-heading a .version {
  color: #738694;
}
.react-mosaic-app > .mosaic {
  height: calc(100% - 50px);
}
.react-mosaic-app .github-link {
  height: 32px;
  width: 32px;
  margin-left: 15px;
}
.react-mosaic-app .toolbar-example {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: left;
          justify-content: left;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  height: 100%;
}
body {
  -moz-user-select: none;
   -ms-user-select: none;
       user-select: none;
  -webkit-user-select: none;
}
.example-window {
  padding: 16px;
}
