.panel-container {
  width: 100%;
  height: 100%;
}
.table-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 0 auto;
          flex: 1 0 auto;
  -webkit-justify-content: center;
          justify-content: center;
}
.form-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  overflow: auto;
  height: 100%;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}
.form-row {
  width: 75%;
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
  display: -webkit-flex;
  display: flex;
  margin-bottom: 0.5rem;
}
.form-row > .fg:not(:last-child),
.form-row .bp3-control-group:not(:last-child) {
  margin-right: 0.5rem;
}
